/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  color: #1a1b1c;
}
body::-webkit-scrollbar {
  background-color: #ffffff;
  width: 0.8rem;
}
body::-webkit-scrollbar-thumb {
  border-radius: 1rem;
  background-color: #1a1b1c;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.container-s {
  width: 95%;
  max-width: 40rem;
  margin: 0 auto;
}

.container-m {
  width: 95%;
  max-width: 75rem;
  margin: 0 auto;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  font-family: "Roboto", sans-serif;
  margin: 0;
  font-weight: 700;
  text-align: center;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.2rem;
}

h3 {
  font-size: 1.8rem;
  text-align: center;
}

p {
  margin: 0;
  font-size: 1.6rem;
}

@media (min-width: 768px) {
  .layout {
    display: grid;
    grid-template-columns: 1fr 4fr;
    min-height: 100vh;
  }
}

.left-bar {
  background-color: rgb(33, 34, 38);
  padding: 5rem 3rem;
}
.left-bar__title {
  font-size: 3rem;
  color: #ffffff;
}
.left-bar__navbar {
  margin-top: 5rem;
}
.left-bar__navbar a {
  display: block;
  margin-bottom: 1.5rem;
  padding: 1rem;
  border-radius: 5px;
  text-align: center;
  font-weight: 500;
  color: #ffffff;
  transition: 0.2s;
}
.left-bar__navbar a:last-of-type {
  margin-bottom: 0;
}
.left-bar__navbar a:hover {
  background-color: #288cf0;
  transform: translateX(1rem);
}
.left-bar__navbar .link-selected {
  background-color: #288cf0;
}

.dashboard {
  padding: 3rem 2rem;
  background-color: #e5e5e5;
  height: 100%;
}
@media (min-width: 768px) {
  .dashboard {
    height: 100vh;
    overflow-y: scroll;
  }
}
.dashboard__title {
  color: #288cf0;
  margin-bottom: 2rem;
}
.dashboard__text {
  margin-bottom: 5rem;
}

.user-form {
  padding: 2rem 4rem;
  border-radius: 5px;
  background-color: #ffffff;
  box-shadow: 5px 5px 10px 0px rgba(39, 40, 41, 0.1);
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.user-form__title {
  color: rgb(33, 34, 38);
  font-weight: 500;
}
.user-form .section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 3rem;
}
.user-form .section :last-of-type {
  margin-bottom: 0;
}
.user-form .section-input {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
@media (min-width: 768px) {
  .user-form .section-input {
    flex-direction: row;
    align-items: center;
  }
}
.user-form .section-input .label {
  width: 100%;
}
@media (min-width: 768px) {
  .user-form .section-input .label {
    max-width: 15rem;
    gap: 1rem;
  }
}
.user-form .section-input .field {
  border-radius: 0.5rem;
  outline: none;
  border: none;
  padding: 1rem 1.5rem;
  background-color: #ebf2f5;
  fill: rgb(33, 34, 38);
}
@media (min-width: 768px) {
  .user-form .section-input .field {
    flex: 1;
  }
}
.user-form .section .error-message {
  display: flex;
  gap: 1rem;
  border-radius: 5px;
  background-color: #fae0e0;
}
.user-form .section .error-message__img {
  border-radius: 5px 0 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
  background-color: #db3333;
}
.user-form .section .error-message__img img {
  width: 3rem;
}
.user-form .section .error-message__text {
  flex: 1;
  padding: 1rem;
}
.user-form .section-button {
  display: flex;
  justify-content: flex-end;
}
.user-form .section-button .form-button {
  border: none;
  border-radius: 0.5rem;
  display: "block";
  padding: 1rem 2rem;
  width: 100%;
  font-size: 1.6rem;
  font-weight: 700;
  text-align: center;
  background: #288cf0;
  color: #ffffff;
  transition: 0.2s;
}
.user-form .section-button .form-button:hover {
  background-color: #0f73d6;
}
.user-form .section-button .form-button.pressed-button {
  transform: scale(0.95);
}
@media (min-width: 768px) {
  .user-form .section-button .form-button {
    max-width: 20rem;
  }
}

.table {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.table__header {
  background-color: #288cf0;
  display: grid;
  padding: 1rem;
  border-radius: 5px;
  grid-template-columns: repeat(4, 1fr);
}
.table__header p {
  color: #ffffff;
  text-align: center;
}
.table__body {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  border-radius: 1rem;
}

.card-user {
  border-radius: 0.5rem;
  padding: 2rem 1rem;
  background-color: #ffffff;
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
  transition-property: background-color;
  transition-duration: 0.2s;
}
@media (min-width: 768px) {
  .card-user {
    grid-template-columns: repeat(4, 1fr);
    gap: 0;
    padding: 1.5rem 0.5rem;
  }
}
.card-user:hover {
  background-color: #def6ff;
}

.card-user__data {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.5rem;
  text-align: center;
}
.card-user__data.contact span {
  font-weight: 700;
  color: #484848;
}
.card-user__data.buttons {
  flex-direction: row;
  gap: 2rem;
}
.card-user__data.buttons button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: none;
  transition: 0.2s;
  position: relative;
}
.card-user__data.buttons button::before {
  position: absolute;
  content: "";
  width: 0;
  bottom: 0;
  left: 0;
  height: 3px;
  background-color: #484848;
  transition: 0.2s;
}
.card-user__data.buttons button:hover::before {
  width: 100%;
}
.card-user__data.buttons button:hover {
  cursor: pointer;
}
.card-user__data.buttons img {
  width: 2.6rem;
}

.user {
  border-radius: 10px;
  background-color: rgb(33, 34, 38);
  color: #ffffff;
  padding: 2rem 3rem;
}
.user__title {
  margin-bottom: 2rem;
}
.user-info {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.user-info span {
  color: #a6a6a6;
}

.error-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}
.error-page__img {
  width: 30rem;
}

.spinner {
  margin: 100px auto;
  width: 40px;
  height: 40px;
  position: relative;
  text-align: center;
  -webkit-animation: sk-rotate 2s infinite linear;
  animation: sk-rotate 2s infinite linear;
}

.dot1,
.dot2 {
  width: 60%;
  height: 60%;
  display: inline-block;
  position: absolute;
  top: 0;
  background-color: #288cf0;
  border-radius: 100%;
  -webkit-animation: sk-bounce 2s infinite ease-in-out;
  animation: sk-bounce 2s infinite ease-in-out;
}

.dot2 {
  top: auto;
  bottom: 0;
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

@-webkit-keyframes sk-rotate {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes sk-rotate {
  100% {
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
  }
}
@-webkit-keyframes sk-bounce {
  0%, 100% {
    -webkit-transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
  }
}
@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}
.sweetalert.title {
  font-size: 2.5rem;
}
.sweetalert.text {
  font-size: 1.8rem;
}
.sweetalert.actions {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .sweetalert.actions {
    width: 36rem;
    flex-direction: row;
    justify-content: space-around;
  }
}
.sweetalert.btn-success, .sweetalert.btn-danger {
  padding: 1rem;
  font-size: 1.6rem;
  border: none;
  border-radius: 0.5rem;
  color: #ffffff;
  transition: 0.3s;
  min-width: 15rem;
}
.sweetalert.btn-success {
  background-color: #288cf0;
}
.sweetalert.btn-success:hover {
  background-color: #0f73d6;
}
.sweetalert.btn-danger {
  background-color: #db3333;
}
.sweetalert.btn-danger:hover {
  background-color: #ba2121;
}